import {AppState} from "../app-state";
import {createSelector} from "reselect";

export const getShop = (state: AppState) => state.shop;
export const getShopName = createSelector(getShop, (shop) => shop.name);
export const getShopCurrency = createSelector(getShop, (shop) => shop.currencyCode);


