import {ReduxBehavior} from "./adv-redux-behavior";
import {fetchShop} from "../state/shop/shop-actions";
import {getShopName} from "../state/shop/shop-selectors";

export class AdvShop {

    get is() {
        return 'adv-shop';
    }

    get behaviors() {
        return [ReduxBehavior];
    }

    get properties() {
        return {
            name: {
                type: String,
                notify: false,
                statePath: getShopName
            },
            product: {
                type: Object, notify: false
            }
        };
    }

    // update(state: AppState) {
    //     this.name = getShopName(state);
    // }

    ready() {
        // this.client.fetchShop().then(shop => this.shop = shop);
        // this.client.fetchHey().then(product => this.product = product);
        this.dispatch(fetchShop());
    }

    attached() {

    }

    _addToCart() {

    }
}

Polymer(AdvShop);



